___CSS_LOADER_EXPORT___.push([module.id, `:root  {
    --white: #fff;
    --black: #000;
    --green: #78BE20;
}

* {
    padding: 0;
    margin: 0;
}

html, body {
    height: 100%;
    color: #000;
}
body{
    display: flex;
    flex-direction: column;
    box-sizing: border-box;
    background-color: #fff;
}

html {
    font-family: ui-monospace, 'Cascadia Code', 'Source Code Pro', Menlo, Consolas, 'DejaVu Sans Mono', monospace;
    font-weight: normal;
}

header {
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    
    padding: 3%;
}

header img {
    max-width: 20%;
}
main {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    flex: 1;
    padding: 0 5%;
}
div {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

    border-radius: 2rem;
    background-color: #78BE20;
    padding: 12% 10%;
}


.permissionBtn {
    --s: 60px; /* the size on the corner */
    --t: 20px;  /* the thickness of the border */
    --g: 0px; /* the gap between the border and image */

    padding: calc(var(--g) + var(--t));
    outline: var(--t) solid #000000; /* the color here */
    outline-offset: calc(-1*var(--t));
    mask:
    conic-gradient(at var(--s) var(--s),#0000 75%,#000 0)
    0 0/calc(100% - var(--s)) calc(100% - var(--s)),
    linear-gradient(#000 0 0) content-box;
    transition: .2s;
}

button {
    cursor: pointer;
    background-color: transparent;
    border: none;
}

button img {
    width: 90%;
}

#test{
    display: none;
}

canvas{
    width: 400px;
    margin-top: 10%;
}

#qrCanvas {
    width: 500px;
    height: 500px;
    background-color: #78BE20;

    --s: 60px; /* the size on the corner */
    --t: 20px;  /* the thickness of the border */
    --g: 0px; /* the gap between the border and image */

    padding: calc(var(--g) + var(--t));
    outline: var(--t) solid #000000; /* the color here */
    outline-offset: calc(-1*var(--t));
    mask:
    conic-gradient(at var(--s) var(--s),#0000 75%,#000 0)
    0 0/calc(100% - var(--s)) calc(100% - var(--s)),
    linear-gradient(#000 0 0) content-box;
    transition: .2s;
}

#captureBtn {
    max-width: 50%;
}

#cameraVideo{
    width: 100%;
    height: 100px;
    object-fit: cover;
}

.hidden {
    display: none;
}

.active {
    display: block
}`, "",{"version":3,"sources":["webpack://./src/style.css"],"names":[],"mappings":"AAAA;IACI,aAAa;IACb,aAAa;IACb,gBAAgB;AACpB;;AAEA;IACI,UAAU;IACV,SAAS;AACb;;AAEA;IACI,YAAY;IACZ,WAAW;AACf;AACA;IACI,aAAa;IACb,sBAAsB;IACtB,sBAAsB;IACtB,sBAAsB;AAC1B;;AAEA;IACI,6GAA6G;IAC7G,mBAAmB;AACvB;;AAEA;IACI,aAAa;IACb,uBAAuB;IACvB,mBAAmB;IACnB,kBAAkB;;IAElB,WAAW;AACf;;AAEA;IACI,cAAc;AAClB;AACA;IACI,aAAa;IACb,sBAAsB;IACtB,uBAAuB;IACvB,mBAAmB;IACnB,OAAO;IACP,aAAa;AACjB;AACA;IACI,aAAa;IACb,sBAAsB;IACtB,uBAAuB;IACvB,mBAAmB;;IAEnB,mBAAmB;IACnB,yBAAyB;IACzB,gBAAgB;AACpB;;;AAGA;IACI,SAAS,EAAE,2BAA2B;IACtC,SAAS,GAAG,gCAAgC;IAC5C,QAAQ,EAAE,yCAAyC;;IAEnD,kCAAkC;IAClC,+BAA+B,EAAE,mBAAmB;IACpD,iCAAiC;IACjC;;;yCAGqC;IACrC,eAAe;AACnB;;AAEA;IACI,eAAe;IACf,6BAA6B;IAC7B,YAAY;AAChB;;AAEA;IACI,UAAU;AACd;;AAEA;IACI,aAAa;AACjB;;AAEA;IACI,YAAY;IACZ,eAAe;AACnB;;AAEA;IACI,YAAY;IACZ,aAAa;IACb,yBAAyB;;IAEzB,SAAS,EAAE,2BAA2B;IACtC,SAAS,GAAG,gCAAgC;IAC5C,QAAQ,EAAE,yCAAyC;;IAEnD,kCAAkC;IAClC,+BAA+B,EAAE,mBAAmB;IACpD,iCAAiC;IACjC;;;yCAGqC;IACrC,eAAe;AACnB;;AAEA;IACI,cAAc;AAClB;;AAEA;IACI,WAAW;IACX,aAAa;IACb,iBAAiB;AACrB;;AAEA;IACI,aAAa;AACjB;;AAEA;IACI;AACJ","sourcesContent":[":root  {\n    --white: #fff;\n    --black: #000;\n    --green: #78BE20;\n}\n\n* {\n    padding: 0;\n    margin: 0;\n}\n\nhtml, body {\n    height: 100%;\n    color: #000;\n}\nbody{\n    display: flex;\n    flex-direction: column;\n    box-sizing: border-box;\n    background-color: #fff;\n}\n\nhtml {\n    font-family: ui-monospace, 'Cascadia Code', 'Source Code Pro', Menlo, Consolas, 'DejaVu Sans Mono', monospace;\n    font-weight: normal;\n}\n\nheader {\n    display: flex;\n    justify-content: center;\n    align-items: center;\n    text-align: center;\n    \n    padding: 3%;\n}\n\nheader img {\n    max-width: 20%;\n}\nmain {\n    display: flex;\n    flex-direction: column;\n    justify-content: center;\n    align-items: center;\n    flex: 1;\n    padding: 0 5%;\n}\ndiv {\n    display: flex;\n    flex-direction: column;\n    justify-cont