import "./style.css";
import { createWorker } from 'tesseract.js';
import QRCode from 'qrcode';

const allowBtn = document.querySelector("#allowBtn");
const cameraDiv = document.querySelector("#camera");
const video = document.querySelector("#cameraVideo");
const captureBtn = document.querySelector("#captureBtn");
const photoCanvas = document.querySelector("#photoCanvas");
const photo = document.querySelector("#photo");
const text = document.querySelector("#ocr-text");

const width = 640; // We will scale the photo width to this
let height = 100; // This will be computed based on the input stream

let streaming = false;


(()=> {
  const vh = window.innerHeight;
  const header = document.getElementById('mainHeader');
  const headerHeight = header ? header.offsetHeight : 0;

  document.body.style.height = vh + "px";
  document.querySelector("main").style.height = (vh - headerHeight) + 'px';
})()


allowBtn.addEventListener("click",()=>{
  let stream = null;
  navigator.mediaDevices
    .getUserMedia({ video: {
      width: { ideal: 1280 },
      height: { ideal: 720 },
      facingMode: "environment"},
      audio: false })
    .then((stream) => {
      cameraDiv.classList.toggle("hidden");
      allowBtn.classList.toggle("hidden");
      video.srcObject = stream;
      video.play();
    })
    .catch((err) => {
      console.error(`An error occurred: ${err}`);
    });
});


video.addEventListener("canplay", (ev) => {
  if (!streaming) {
    video.setAttribute("width", width);
    video.setAttribute("height", height);
    photoCanvas.setAttribute("width", width);
    photoCanvas.setAttribute("height", height);
    streaming = true;
  }
});


captureBtn.addEventListener("click", (ev) => {
  takePicture();
  ev.preventDefault();
});


function clearPhoto() {
  const context = photoCanvas.getContext("2d", { willReadFrequently: true });
  context.fillStyle = "#aaaaaa";
  context.fillRect(0, 0, photoCanvas.width, photoCanvas.height);

  const data = photoCanvas.toDataURL("image/png");
  photo.setAttribute("src", data);
}
clearPhoto();


function takePicture() {
  const context = photoCanvas.getContext("2d", { willReadFrequently: true });
  if (width && height) {
    photoCanvas.width = width;
    photoCanvas.height = height;

    context.drawImage(video, 0, 0, width, height);
    preprocess(photoCanvas);

    const data = photoCanvas.toDataURL("image/png");
    photo.setAttribute("src", data);

    cameraDiv.classList.toggle("hidden");
    allowBtn.classList.toggle("hidden");
    displayQr();
  } else {
    clearPhoto();
  }
}


// Tesseract and qr creation
async function displayQr() {
  const worker = await createWorker('eng');
  await worker.setParameters({
    tessedit_char_whitelist: '0123456789'
  });

  const ret = await worker.recognize(photo);
  await worker.terminate();

  const rawText = ret.data.text;
  console.log(rawText);
  text.textContent = rawText;

  const matches = rawText.match(/\b991\d{14}\b/g);
  console.log(matches);
  const qrContainer = document.querySelector("#qrContainer"); // a div to hold all QRs
  qrContainer.innerHTML = ""; // clear previous

  if (matches) {
    matches.forEach((code) => {
      const canvas = document.createElement("canvas");
      qrContainer.appendChild(canvas);
      QRCode.toCanvas(canvas, code, { width: 250 }, (error) => {
        if (error) console.error(error);
      });
    });
  } else {
    // fallback: one QR from raw text
    const canvas = document.createElement("canvas");
    qrContainer.appendChild(canvas);
    QRCode.toCanvas(canvas, rawText.trim(), { width: 250 }, (error) => {
      if (error) console.error(error);
    });
  }
}


function threshold(ctx, canvas, level = 150) {
  const imgData = ctx.getImageData(0, 0, canvas.width, canvas.height);
  const data = imgData.data;

  for (let i = 0; i < data.length; i += 4) {
    const v = data[i] < level ? 0 : 255;
    data[i] = data[i+1] = data[i+2] = v;
  }

  ctx.putImageData(imgData, 0, 0);
}


function toGrayscale(ctx, canvas) {
  const imgData = ctx.getImageData(0, 0, canvas.width, canvas.height);
  const data = imgData.data;

  for (let i = 0; i < data.length; i += 4) {
    const gray = 0.299 * data[i] + 0.587 * data[i+1] + 0.114 * data[i+2];
    data[i] = data[i+1] = data[i+2] = gray;
  }
  ctx.putImageData(imgData, 0, 0);
}


function increaseContrast(ctx, canvas, factor = 1.4) {
  const imgData = ctx.getImageData(0, 0, canvas.width, canvas.height);
  const data = imgData.data;

  const midpoint = 128;
  for (let i = 0; i < data.length; i += 4) {
    for (let c = 0; c < 3; c++) {
      let v = data[i + c];
      v = midpoint + (v - midpoint) * factor;
      data[i + c] = Math.max(0, Math.min(255, v));
    }
  }

  ctx.putImageData(imgData, 0, 0);
}

function applyBlur(ctx, canvas) {
  ctx.filter = 'blur(1px)';
  ctx.drawImage(canvas, 0, 0);
  ctx.filter = 'none';
}

function preprocess(canvas) {
  const ctx = canvas.getContext("2d", { willReadFrequently: true });

  // 1. grayscale
  toGrayscale(ctx, canvas);

  // 2. blur
  applyBlur(ctx, canvas);

  // 3. increase contrast
  increaseContrast(ctx, canvas, 1.2);

  // 4. threshold (optional but often helps)
  threshold(ctx, canvas, 30);

  return canvas;
}

